.Form {
    padding: 20px;
}

.Header {
    text-align: center;
    font-size: 30px;
    font-weight: bolder;
}

.ErrorMessage {
    font-size: 1rem !important;
    font-weight: bolder;
}

.Button, .Input {
    border: 0;
    outline: 0;
    font-size: 16px;
    border-radius: 320px;
    padding: 16px;
    background-color: #EBECF0;
    text-shadow: 1px 1px 0 #FFF;
}

.Input {
    margin-right: 32px;
    box-shadow: inset 2px 2px 5px #BABECC, inset -5px -5px 10px #FFF;
    width: 100%;
    box-sizing: border-box;
    transition: all 0.2s ease-in-out;
    appearance: none;
    -webkit-appearance: none;
}

.Input:focus {
    box-shadow: inset 1px 1px 2px #BABECC, inset -1px -1px 2px #FFF;
}

body, header, .Input, .Button {
    font-family: 'Montserrat', sans-serif;
    letter-spacing: -0.2px;
    font-size: 16px;
}

.Button {
    color: #61677C;
    font-weight: bold;
    box-shadow: 3px 3px 10px #FFF, 3px 3px 10px #000000;
    transition: all 0.2s ease-in-out;
    cursor: pointer;
    font-weight: 600;
}

.Button:hover {
    box-shadow: -2px -2px 5px #FFF, 2px 2px 5px #000000;
}

.Button:active {
    box-shadow: inset 1px 1px 2px #BABECC, inset -1px -1px 2px #000000;
}